* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

html, 
body {
  background-color: #1b1b32;
}

.App {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: center;
  padding-top: 15px;
  font-family: Lato, sans-serif;

}

.freecodecamp-logo-contenedor {
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 20px;
}

.freecodecamp-logo {
  cursor: pointer;
  height: 50px;
}

.contenedor-principal {
  height: 550px;
  min-width: 600px;
  display: flex;
  flex-wrap: wrap;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

footer{
  background-color: #16213E;
  font-family: Roboto, sans-serif;
  width: 100%;
  height: 100px;
  position:absolute;
  bottom: 0;
  color: #F9F9F9;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

span{
  margin: 5px;
}
.contenedorInformacion{
  display: flex;
  justify-content: space-around;
  align-items: center;
}
.icon{
  margin: 5px;
  width: 30px;
  height: 30px;
  color: #F9F9F9;
}


